.App {
    font-size: x-large;
    margin-left: 25px;
}

html {
    background-color: rgb(179, 205, 228);
}

div {
    margin-top: 15px;
}

label {
    margin-right: 5px;
}

input,
select {
    font-size: large;
}

input[type='button'] {
    background-color: rgb(30, 100, 192);
    color: white;
    border-radius: 5%;
    border: 5px solid white;
    padding: 15px;
}

input[type='button']:hover {
    background-color: rgb(80, 156, 255);
    color: rgb(239, 239, 239);
    cursor: pointer;
}